.review-list {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;

  .review {
    width: 90px;
    margin-right: 10px;

    &:last-child {
      margin-right: 0 !important;
    }

    @media screen and (max-width: 775px) {
      width: 50%;
    }

    .img {
      width: 90px;
      height: 92px;
      @media screen and (max-width: 775px) {
        width: 100%;
      }
    }

    a {
      text-decoration: none;
      color: black;
    }
  }
}