.review-detail {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 13px;

  @media screen and (max-width: 775px) {
    width: 90%;
    margin: 0 auto;
  }

  .title {
    font-size: 18px;
    font-weight: normal;
    line-height: 1.2;
    background: #999;
    color: #fff;
    padding: 13px 0 0 16px;
    height: 35px;
  }

  .review-info {
    margin-top: 10px;
  }

  .review-image {
    margin-bottom: 10px;
    height: 300px;
  }

  .review-contents {
    overflow-y: scroll;
  }
}